/* The Close Button */
.close {
    color: black;
    float: right;
    font-size: 28px;
    font-weight: bold;
    padding: 10px;
}

.close:hover,
.close:focus {
    color: white;
    text-decoration: none;
    cursor: pointer;
}

/* Animation shade */

.shade-enter {
    opacity: 0.01;
}

.shade-enter-active {
    opacity: 1;
    transition: all 100ms ease-out;
}

.shade-exit {
    opacity: 1;
}

.shade-exit-active {
    opacity: 0.01;
    transition: all 100ms ease-out;
}

/* Animation popup */

.popup-enter {
    opacity: 0.01;
    -webkit-transform: scale(0.9) translateY(50%);
            transform: scale(0.9) translateY(50%);
}

.popup-enter-active {
    opacity: 1;
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
    transition: all 200ms ease-out;
}

.popup-exit {
    opacity: 1;
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
}

.popup-exit-active {
    opacity: 0.01;
    -webkit-transform: scale(0.9) translateY(50%);
            transform: scale(0.9) translateY(50%);
    transition: all 200ms ease-out;
}



/* ModalRecherche Content */
.modal-content {
    position: fixed;
    bottom: 0;
    background-color: #fefefe;
    width: 100%;
    height: 100%;
}

/* The Close Button */
.close {
    color: black;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: white;
    text-decoration: none;
    cursor: pointer;
}

.modal-header {
    padding: 2px 16px;
    background-color: #D4C3B5;
    color: black;
}

.modal-header input {
    background: transparent;
    border: none;
    font-size: 20px;

}

/* Animation Menu */

.popup-enter {
    opacity: 0.01;
    -webkit-transform: scale(0.9) translateY(50%);
            transform: scale(0.9) translateY(50%);
}

.popup-enter-active {
    opacity: 1;
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
    transition: all 200ms ease-out;
}

.popup-exit {
    opacity: 1;
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
}

.popup-exit-active {
    opacity: 0.01;
    -webkit-transform: scale(0.9) translateY(50%);
            transform: scale(0.9) translateY(50%);
    transition: all 200ms ease-out;
}

/* Animation list item */

.fade-enter {
    opacity: 0.01;
}

.fade-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0.01;
    transition: opacity 500ms ease-in;
}





